import React from "react";
import { Slider, Icon } from 'antd';
import styled from 'styled-components';
import { mysky, mycharcoal } from '../Colors';

const Tray = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: center;
  justify-content: space-between;
`;

const SliderName = styled.p`
  color: ${mycharcoal};
`;

const SliderBox = styled.div`
  width: 100%;
  /* .anticon {
    position: absolute;
    margin-top: 0px;
    top: -2px;
    width: 16px;
    height: 16px;
    line-height: 1;
    color: white;
    &:first-child {
      left: 0;
    }
    &:last-child {
      right: 0;
    }
  } */
  .ant-slider {
    margin-top: 0;
  }
  .ant-slider-track {
    background-color: ${mysky};
  }
  .ant-slider-handle {
    border-color: ${mysky};
  }
`;

const SliderInput = (props) => {
  const { 
    property,
    handleChange,
    name,
    max,
    min,
    defaultValue,
    formatter,
    // firstIcon,
    // secondIcon 
  } = props;

  const changeSetting = (value) => {
    return (
      handleChange(name, property, value)
    )
  };

  return (
    <Tray>
      <SliderName>{property}</SliderName>
      <SliderBox>
        {/* <Icon type={firstIcon} /> */}
        <Slider 
          onChange={changeSetting}
          defaultValue={defaultValue}
          min={min}
          max={max}
          tipFormatter={formatter} />
        {/* <Icon type={secondIcon} /> */}
      </SliderBox>
    </Tray>
  )
};

export default SliderInput;

